import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import './Technician.css';

function TechPage() {
  const history = useHistory();

  const [name, setName] = useState("");
  const [date, setDate] = useState("");
  const [unitSN, setunitSN] = useState("");
  const [tests, setTests] = useState([]);

  function handleChange(e) {
    const inputName = e.target.name;
    const inputVal = e.target.value;
    
    switch (inputName) {
      case "name":
        setName(inputVal);
        break;
      case "startDate":
        setDate(inputVal);
        break;
      case "unitSN":
        setunitSN(inputVal);
        break;
      case "generalRes":
        console.log("general checxked");
        setTests({
          ...tests,
          generalRes: inputVal
        });
        break;
      default:
        console.log("no input name chosen " + inputName + " " + inputVal);
        break;
    }
  }
    
  const handleCheckboxChange = event => {
    const inputChecked = event.target.checked;
    const inputVal = event.target.name;
    
    if (inputChecked) {
          setTests([
            ...tests,
            inputVal
          ]);
    }
  }

  function handleClick (e) {
    e.preventDefault()
    history.push({
      pathname: '/results',
      state: {
        techName: name,
        testDate: date,
        techUnitSN: unitSN,
        testsChosen: tests
      }
    }); 
  }

  return (
    <div className="tech-container" >
      <h1 className="header">Technician Page</h1>
      
        <form action="/search" method="POST">
          <div className="search-fields">
            <div className="name-input">
              <p className="name">name :</p>
              <input className="name-input-field"
              name="name" 
              autoComplete="off"
              onChange={handleChange}/>
            </div>
            <br />
            <div className="name-input">
              <p className="name">date :</p>
              <input className="name-input-field"
                type="date"
                name="startDate" 
                autoComplete="off"
                onChange={handleChange}/>
            </div>
          <br />
           <div className="name-input">
              <p className="name">unit SN :</p>
              <input className="name-input-field"
                name="unitSN" 
                autoComplete="off"
                onChange={handleChange}/>
          </div>
          <br/>
          </div>
            <div className="tests-input">
              <p className="tests-header">tests :</p>
              
              <label className="checkbox-lbl test1">
                <input
                  type="checkbox"
                  name="firstPrepRes"
                  className="test-checkbox"
                  onChange={handleCheckboxChange}
                />
                <span className="test-text">First-General-Prep-test</span>
            </label>
            
            <label className="checkbox-lbl test2">
                <input
                  type="checkbox"
                  name="imuGpsRes"
                  className="test-checkbox"
                  onChange={handleCheckboxChange}
                />
                <span className="test-text">IMU-test</span>
            </label>
            
            <label className="checkbox-lbl test3">
                <input
                  type="checkbox"
                  name="TcxoCalRes"
                  className="test-checkbox"
                  onChange={handleCheckboxChange}
                />
                <span className="test-text">TCXO-calibration</span>
            </label>
            
            <label className="checkbox-lbl test4">
                <input
                  type="checkbox"
                  name="p1dbRes"
                  className="test-checkbox"
                  onChange={handleCheckboxChange}
                />
                <span className="test-text">P1db-test</span>
            </label>
            
            <label className="checkbox-lbl test5">
                <input
                  type="checkbox"
                  name="curConsRes"
                  className="test-checkbox"
                  onChange={handleCheckboxChange}
                />
                <span className="test-text">Current-consumption</span>
            </label>
            
            <label className="checkbox-lbl test6">
                <input
                  type="checkbox"
                  name="fulLinkRes"
                  className="test-checkbox"
                  onChange={handleCheckboxChange}
                />
                <span className="test-text">FullLink</span>
            </label>
            
            <label className="checkbox-lbl test7">
                <input
                  type="checkbox"
                  name="crossPollRes"
                  className="test-checkbox"
                  onChange={handleCheckboxChange}
                />
                <span className="test-text">cross-poll</span>
            </label>
            
            <label className="checkbox-lbl test8">
                <input
                  type="checkbox"
                  name="generalRes"
                  className="test-checkbox"
                  onChange={handleCheckboxChange}
                />
                <span className="test-text">General results</span>
            </label>
                        
                          
            </div>
            <br />
           
          
          <div>
            <button type="button" onClick={handleClick} className="search-button">
              Search
            </button>          
          </div>
        </form>
    </div>
  );
}

export default TechPage;
